import "./Videos.scss";
import { FC, useEffect, useMemo, useState } from "react";
import "react-awesome-slider/dist/styles.css";
import classNames from "classnames";
import { Loading_icon } from "../../images/Loading";
import { Icon_like } from "../../images/index/like";
import { CommentPage } from "../Creator/Comment";
import { Icon_like_on } from "../../images/index/likeOn";
import { translate } from "../../components/Text";
import { RouteComponentProps, useHistory } from "react-router";
import { useRequest, useCurrentUser } from "../../utils/hooks";
import {
  CreatorCollectionDetailService,
  CreatorVideoItem,
  CreatorVideoService,
} from "../../services/CreatorVideoService";
import { VideoLikeService, VideoService } from "../../services/VideoService";
import { Loading } from "../../components/Loading";
import { CreatorSubscribeListService, CreatorSubscribeService, CreatorUserService } from "../../services/CreatorService";
import { observer } from "mobx-react";
import { userStore } from "../../stores/UserStore";
import './Videos.scss';
import 'react-awesome-slider/dist/styles.css';
import { Avatar, Image } from "../../components/Image";
import { StripePayArea } from "../Stripe/Stripe";
import { CollectionDataService } from "../../services/CollectionListService";
import { LocalStorageService } from "../../utils/storage";
import GlobalConfig from "../../config/GlobalConfig";
import IconCry from '../../images/creator/cry.png';
import { divide } from "lodash";

function itemTrans(it: any) {
  return { ...it, lookauth: it.islookauth };
}

function getPlayVideo(id: string): Promise<any> {
  return new CreatorVideoService({ id }).send();
}

function getVideoList({ type = "c", cid = "" }) {
  if (type === "c") {
    return Promise.all([
      new CollectionDataService(cid).send(),
      new CreatorCollectionDetailService(cid).send()
    ]).then(([collInfo, list]) => {
      if (!collInfo) {
        return Promise.reject(new Error('未能找到指定视频合集'));
      }
      return {
        info: { name: `${collInfo.name}` },
        list: list.map((it) => ({ ...it, price: collInfo.price, collectid: collInfo.id }))
      };
    });
  } else {
    let resPromise: Promise<CreatorVideoItem[]>;
    if (type === "l") {
      resPromise = new VideoService(1, 8, { labelid: cid })
        .send()
        .then((res) => res.map(itemTrans));
    } else if (type === 'up') {
      resPromise = new CreatorVideoService({ isfree: 1, psize: 999, userid: cid }).send();
    } else if (type === 'uf') {
      resPromise = new CreatorVideoService({ isfree: 0, psize: 999, userid: cid }).send();
    } else if (type === 'ua') {
      resPromise = new CreatorVideoService({ psize: 999, userid: cid }).send();
    } else if (type === 'hot') {
      resPromise = new CreatorVideoService({ ishot: 1, psize: 8 }).send();
    } else {
      resPromise = new VideoService(1, 6)
        .send()
        .then((res) => res.map(itemTrans));
    }
    return resPromise.then(list => ({ list, info: { name: translate('home.video.e') } }));
  }
}

function sendVideoLike({ id = "", islike = 0 }) {
  return new VideoLikeService(id, islike).send();
}

const getUserData = ({ id = "" }) => new CreatorUserService(id).send();

const getAllFollowIds = () => new CreatorSubscribeListService({ userid: userStore.userInfo?.id, psize: 999 }).send().then(res => res.map(it => it.id));

const doFollow = ({ userid = '', status = '' }) => new CreatorSubscribeService({ objectid: userid, objecttype: 1, status }).send();

const AuthPage = observer(({ userid = "" }) => {
  const his = useHistory();
  const { data: user, loading, load } = useRequest(getUserData, { autoLoad: false });
  const { data: allFollowIdsData, loading: foloading } = useRequest(getAllFollowIds);

  const [allFollowIds, setAllFollowIds] = useState([] as string[]);
  const [hasFollowOnInit, setHasFollowOnInit] = useState(false);

  useEffect(() => load({ id: userid }), [userid]);
  useEffect(() => {
    setAllFollowIds(allFollowIdsData ?? []);
    setHasFollowOnInit((allFollowIdsData ?? []).indexOf(userid) > -1);
  }, [allFollowIdsData]);

  const hasFollow = (allFollowIds ?? []).indexOf(userid) > -1;
  const isFollowCountDiff = hasFollow !== hasFollowOnInit;
  const hasLogin = !!userStore.userInfo;

  const doSub = () => {
    if (!hasLogin) {
      his.push('/login');
    } else if (hasFollow) {
      setAllFollowIds(allFollowIds.filter(i => i !== userid));
      doFollow({ userid, status: '0' });
    } else {
      setAllFollowIds(allFollowIds.concat([userid]));
      doFollow({ userid, status: '1' });
    }
  };

  if (!user || loading || foloading) {
    return <div />
  }

  return (
    <div className="user-info">
      <Avatar
        width={50}
        height={50}
        src={user.avatar}
        onClick={() => his.push(`/main/userCenter/${userid}/profile`)}
      />
      <div className="user-tx-info">
        <div className="author">{user.username}</div>
        <div className="desc">
          {Math.max(0, parseInt(user.fanscount ?? '0', 10) + (!isFollowCountDiff ? 0 : (hasFollow ? 1 : -1)))} {translate("creator.myCenter.2")}
        </div>
      </div>
      {userid !== userStore.userInfo?.id && (
        <button className={classNames('sub-btn', { cancel: hasFollow })} onClick={doSub}>
          {translate(`creator.author.${hasFollow ? '2' : '1'}`)}
        </button>
      )}
    </div>
  );
});

export const VideosPlayPage: FC<
  RouteComponentProps<{ cid: string; id: string }>
> = (props) => {
  let { cid, id } = props.match.params;
  const his = useHistory();
  const user = useCurrentUser();

  const { data: rawData, load } = useRequest(getVideoList, { autoLoad: false });
  const { loadWithPromise: doLike } = useRequest(sendVideoLike, {
    autoLoad: false,
  });

  const data = useMemo(() => (rawData?.list ?? []).map(it => ({ ...it, lookauth: user?.id === it.userid ? 1 : it.lookauth })), [rawData, user?.id]);

  const vidoe: any = id && id !== "0" ? data.find((it) => it.id == id) : data[0];
    console.log("vidoe",vidoe);
  const [islike, setIslike] = useState(0);
  const [needBuy, setNeedBuy] = useState(false);
  const [buyTips, setBuyTips] = useState(false);
  const isCollMode = /^c(\d+)$/.test(cid);

  useEffect(() => {
    if (vidoe?.id && vidoe?.id !== '0') {
      getPlayVideo(vidoe.id);
    }
  }, [vidoe]);

  const reload = () => {
    if (cid === "hot") {
      load({ type: "hot" });
      return;
    }
    if (cid === 'index') {
      load({ type: 'index' });
      return;
    }
    const [_, type, tid] = /^(c|l|up|uf|ua)(\d+)$/.exec(cid) ?? [];
    if (type) {
      load({ type, cid: tid });
    }
  };

  const checkNeedBuy = (currentTime: number) => {
    const tryminute = parseInt(vidoe?.tryminute ?? '0', 10);
    // 如果视频允许观看
    if (vidoe?.lookauth === 1) {
      setBuyTips(false);
      setNeedBuy(false);
    }
    // 如果视频设置了试看时间
    else if (tryminute > 0) {
      setBuyTips(true);
      setNeedBuy(currentTime > tryminute * 60);
    }
    // 如果没设置试看时间
    else {
      setBuyTips(false);
      setNeedBuy(true);
    }
  }

  useEffect(reload, [cid]);

  useEffect(() => {
    if (vidoe) {
      setIslike(vidoe.islike);
      checkNeedBuy(0);
    }
  }, [vidoe]);
  return (
    <div className="videoPlayPage">
      {!vidoe ? (
        <div className="loading">
          <Loading />
        </div>
      ) : (
        <div className="videoWrapper">
          <div className="leftContent">
            <div className="playContent">
              {buyTips && !needBuy && <div className="buyTips"><span> 试看6分钟，观看完整版请</span>
                <StripePayArea
                  disableMask={true}
                  targetInfo={{ title: isCollMode ? (rawData?.info.name || '') : vidoe?.title, avatar: vidoe?.avatar, subTitle: vidoe?.username }}
                  objectid={isCollMode ? vidoe.collectid : vidoe.id}
                  ordertype={isCollMode ? 4 : 3}
                  price={vidoe.price}
                  onComplete={() => {
                    reload();
                  }}
                ><span className="buy">购买本片</span></StripePayArea>  </div>}
              {needBuy ? (
                <div className="video-pay">
                  <h1>{translate('creator.myVideos.47')}</h1>
                  <span className="price">${vidoe?.price}</span>
                  <img src={IconCry} />
                  <StripePayArea
                    disableMask={true}
                    targetInfo={{ title: isCollMode ? (rawData?.info.name || '') : vidoe?.title, avatar: vidoe?.avatar, subTitle: vidoe?.username }}
                    objectid={isCollMode ? vidoe.collectid : vidoe.id}
                    ordertype={isCollMode ? 4 : 3}
                    price={vidoe.price}
                    onComplete={() => {
                      reload();
                    }}
                  >
                    <a className="pay-btn">{translate('creator.myVideos.48')} &gt;&gt;</a>
                  </StripePayArea>
                </div>
              ) : (
                <video
                  onTimeUpdate={(evt) => checkNeedBuy(evt.currentTarget.currentTime)}
                  controlsList="nodownload"
                  src={vidoe.url}
                  controls={true}
                  autoPlay={true}
                  muted={true}
                ></video>
              )}
            </div>
            <div className="info">
              <div className="text">
                <div className="title">{vidoe.title}</div>
                <div className="view">
                  {vidoe.viewcount}
                  {translate("home.video.a")}
                </div>
              </div>
              <div
                className="like"
                onClick={() => {
                  let isLogin = LocalStorageService.get("token");
                  if (!isLogin) {
                    GlobalConfig.history.push('/login');
                    return;
                  }
                  setIslike(islike ? 0 : 1);
                  doLike({ id: vidoe.id, islike: islike ? 0 : 1 }).then(reload);
                }}
              >
                <img
                  className="left"
                  src={islike == 1 ? Icon_like_on : Icon_like}
                  alt=""
                />
                <div className="likeCount left">{vidoe.likecount}</div>
              </div>
            </div>
            <AuthPage userid={vidoe.userid} />
            <CommentPage objectid={vidoe.id} objecttype={5} />
          </div>
          <div className="rightContent">
            <h1>{rawData?.info.name}</h1>
            {data.map((l: any, i) => (
              <StripePayArea
                key={i}
                targetInfo={{ title: rawData?.info.name || '', avatar: l.avatar, subTitle: l.username }}
                objectid={isCollMode ? l.collectid : l.id}
                ordertype={isCollMode ? 4 : 3}
                price={'0'}
                onClick={() => {
                  // videoStore.getDetail(l.id);
                  his.push(l.id);
                }}
                onComplete={() => {
                  reload();
                  his.push(l.id);
                }}
                className={classNames("item", { active: vidoe.id === l.id })}
              >
                <div className="video-thumb-wrapper">

                  <Image className="video-thumb" src={l.thumb} alt="" />
                  {isCollMode && l.lookauth === 1 && <div className="free">Free</div>}
                </div>
                <div className="info">
                  <div className="title">{l.title}</div>
                  <div className="view">
                    {l.viewcount} {translate("home.video.a")}
                  </div>
                  <div className="date">{l.createtime}</div>
                </div>
              </StripePayArea>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};
