import { Col, Row } from 'antd';
import { Modal, Button } from '../UserCenter/Common';
import { observer } from 'mobx-react';
import { stripeStore } from '../../stores/StripeStore';
import { loadStripe } from '@stripe/stripe-js';
import {
    CardElement,
    CardNumberElement,
    CardExpiryElement,
    CardCvcElement,
    Elements,
    useStripe,
    useElements,
} from '@stripe/react-stripe-js';
import classNames from 'classnames';

import './Stripe.scss';
import { showError, showLoading, showSuccess } from '../../components/Message';
import { translate } from '../../components/Text';
import { Component, FC, useEffect, useState } from 'react';
import { useHistory } from 'react-router';
import { userInfo } from 'os';
import { userStore } from '../../stores/UserStore';
import { IconVideoPlay } from '../UserCenter/Icons';
import { px } from '../../utils/rem';
import { mainStore } from '../../stores/MainStore';
import GlobalConfig from '../../config/GlobalConfig';

export interface TargetInfo { title: string; avatar: string; subTitle: string };

interface EditModalProps {
    targetInfo?: TargetInfo;
    visible: boolean;
    getSercet?: Function;
    cancle: Function;
    success: Function;
}

export const StripeModal = observer((props: EditModalProps) => {
    const { targetInfo } = props;
    const [stripePromise, setStripePromise] = useState<any>();
    const isEn = mainStore.isEn;
    useEffect(() => {
        setStripePromise(loadStripe(GlobalConfig.stripeKey, {
            locale: isEn ? 'en' : 'zh'
        }));
    }, [isEn]);
    return (
        <Modal
            title={translate("payment.1")}
            wrapClassName="stripe-modal"
            visible={props.visible}
            width={px(510)}
            footer={null}
            centered={true}
            onCancel={() => props.cancle()}
        >
            <div className="stripe-bill-info">
                {targetInfo && (
                    <div className="target-info">
                        <img className="avatar" src={targetInfo.avatar} />
                        <div>
                            <h1>{targetInfo.title}</h1>
                            <div className="desc">{targetInfo.subTitle}</div>
                        </div>
                    </div>
                )}

                <div className="price-area">
                    <span>{translate("payment.2")}:</span>
                    <span>${stripeStore.amount}</span>
                </div>
            </div>
            <Elements stripe={stripePromise}>
                <CheckoutForm success={props.success} getSercet={props.getSercet} />
            </Elements>
            {/* <form className='stripeForm' method="post" id="payment-form" >
                <div className="form-row">
                    <div id="card-number">
                    </div>

                    <div id="card-errors" role="alert"></div>
                </div>

                <Button htmlType="submit" className="login-form-button" >
                    支付 ${stripeStore.amount}
                </Button>
            </form> */}
        </Modal>
    );
})





interface CheckFormProps {
    success: Function;
    getSercet?: Function;
}

const CheckoutForm = observer((props: CheckFormProps) => {
    const stripe = useStripe();
    const elements = useElements();

    const handleSubmit = async (event: any) => {
        event.preventDefault();
        if (elements == null) {
            return;
        }
        let element = elements.getElement(CardNumberElement);
        if (stripe && element) {
            let loading = showLoading(translate('gold.form.d'));
            try {
                const { error, paymentMethod } = await stripe.createPaymentMethod({
                    type: 'card',
                    card: element,
                });
                console.log("paymentMethod", paymentMethod)
                let secret = props.getSercet ? (await props.getSercet()) : (await stripeStore.getOrder());
                console.log("secretsecret", secret)
                stripe.confirmCardPayment(secret, { payment_method: { card: element } }).then(function (result) {
                    console.log("result", result)
                    //此处data.client_secret变量为生成订单后 后端返回给前端的秘钥  将这个秘钥返回给stripe去做校验
                    if (result.paymentIntent && result.paymentIntent.status == "succeeded") {
                        //支付成功的逻辑操作
                        loading();
                        props.success && props.success();
                    } else {
                        showError(translate('gold.form.e', { value: '' }));
                    }
                })
            } catch (e) {
                showSuccess(translate('gold.form.e', { value: `${e}` }));

            }


        }
    };

    return (
        <form onSubmit={handleSubmit} className="stripe-form">
            <label>{translate("payment.3")}</label>
            <CardNumberElement />
            <Row gutter={10}>
                <Col span={12}>
                    <label>{translate("payment.4")}</label>
                    <CardExpiryElement />
                </Col>
                <Col span={12}>
                    <label>{translate("payment.5")}</label>
                    <CardCvcElement />
                </Col>
            </Row>
            <div className="center">
                <Button htmlType="submit" className="stripeSubmit" >
                    {translate("payment.6")}
                </Button>
            </div>
            <div className="extra-info">
                {translate("payment.7")} <a target="_blank" href="https://aolinews.oss-ap-southeast-2.aliyuncs.com/pdf/subscription.pdf">{translate("payment.8")}</a>
            </div>
        </form>
    );
});

export interface StripePayAreaProps {
    price: string;
    objectid: string;
    ordertype: number;
    className?: string;
    onClick?: () => void;
    onComplete?: () => void;
    disableMask?: boolean;
    targetInfo?: TargetInfo;
}

export const StripePayArea: FC<StripePayAreaProps> = (props) => {
    const [visible, setVisible] = useState(false);
    const his = useHistory();
    const needBuy = parseFloat(props.price) > 0;
    const doClick = () => {
        if (needBuy) {
            if (!userStore.userInfo) {
                his.push('/login');
                return;
            }
            stripeStore.objectid = props.objectid;
            stripeStore.ordertype = props.ordertype;
            stripeStore.pay(props.price);
            setVisible(true);
        } else if (props.onClick) {
            props.onClick();
        }
    };
    const afterPay = () => {
        setVisible(false);
        showSuccess('支付完成');
        if (props.onComplete) {
            props.onComplete();
        }
    }

    return (
        <div className={classNames('stripe-area', props.className)} onClick={!needBuy ? props.onClick : undefined}>
            {props.children}
            <div className={classNames("price-mask", { 'optc': props.disableMask })} onClick={doClick}>
                {needBuy ? <span>$ {props.price}</span> : <div className="icon-play">{IconVideoPlay}</div>}
            </div>
            {needBuy && <StripeModal
                visible={visible}
                success={afterPay}
                targetInfo={props.targetInfo}
                cancle={() => {
                    setVisible(false);
                }}
            />}
        </div>
    );
};







