import { makeAutoObservable, } from "mobx";
import { showError } from "../components/Message";
import { GoldApplyService, GoldItem, GoldService } from "../services/GoldService";
import { StripeOrder, StripeOrderService } from "../services/StripeOrderService";

export const GoldInfo = {
  'Gold': { title:"gold.title.a", info: "gold.description.a" },
  'Silver': { title: "gold.title.b", info: "gold.description.b" },
  'Platinum': { title: "gold.title.c", info: "gold.description.c"  },
  'Palladium': { title: "gold.title.d", info: "gold.description.d" },
}

class GoldStore {
  public list: GoldItem[] = [];
  public items: GoldItem[] = [];
  public tabs: string[] = [];
  public type: string = 'Gold';
  public purchaseData: any = { advance_deposit: 2000 };
  public loading: boolean = false;
  public showStripe: boolean = false;
  public paySuccess: boolean = false;
  public payModalVisible: boolean = false;

  constructor() {
    makeAutoObservable(this);
  }

  public async purchase(): Promise<string> {
    let applyId: string;
    applyId = await new GoldApplyService(this.purchaseData).send();
    let res: StripeOrder;
    let _amount = this.purchaseData.advance_deposit * 100;
    try {
      res = await new StripeOrderService(`${_amount}`, 6, applyId).send();
    } catch (e) {
      throw e;
    }
    return res.client_secret;
  }

  public async changeType(type:string){
    this.type = type;
    this.loading = true;
    const sleep = (timeountMS:number) => new Promise((resolve) => {
      setTimeout(resolve, timeountMS);
    });
    await sleep(200);
    this.loading = false;
    this.items = goldStore.list.filter((l) => l.type == type);
  }

  public async getData() {
    if (this.loading) {
      return;
    }
    let response: GoldItem[];

    this.loading = true;
    try {
      response = await new GoldService().send();
    
    } catch (e: any) {
      this.loading = false;
      showError(e);
      return;
    }
    this.loading = false;
    this.list = response;
    let typeMap = {}
    response.forEach((r, i) => {
      typeMap[r.type] = '';
    })
    this.tabs = Object.keys(typeMap);
    this.type = this.tabs[0];
    this.items = goldStore.list.filter((l) => l.type == this.type);
  }
}

const goldStore = new GoldStore();

export { goldStore };