import { FC, useEffect } from "react"
import { useParams } from "react-router";
import { Loading } from "../../components/Loading";
import NoData from "../../components/NoData";
import GlobalConfig from "../../config/GlobalConfig";
import { translate } from "../../components/Text";
import { CreatorFansService, CreatorSubscribeListService, CreatorSubscribeService, CreatorUser } from "../../services/CreatorService";
import { useCurrentUser, useRequest } from "../../utils/hooks";
import { MyCenterHeader } from "./Common"
import { IconChecked } from "./Icons";
import { userStore } from "../../stores/UserStore";

const getFollowing = () => new CreatorSubscribeListService({ userid: userStore.userInfo?.id, psize: 999 }).send()

const getFollowers = () => new CreatorFansService({ userids: userStore.userInfo?.id, psize: 1 }).send();

const FollowerList: FC<{ list: CreatorUser[] }> = ({ list }) => {
  return (
    <div className="follower-list">
      {list.map(it => (
        <div className="follower-item" key={it.id} onClick={()=>{
          GlobalConfig.history.push(`/main/userCenter/${it.id}/videoList`)
        }}>
          <img className="avatar" src={it.avatar} />
          <div className="follower-info">
            <h1>{it.username} {IconChecked}</h1>
            <div className="info">
              <div className="item">
                <div className="count">{it.fanscount}</div>
                <div>{translate("creator.myCenter.2")}</div>
              </div>
              <div className="item">
                <div className="count">{it.followcount}</div>
                <div>{translate("creator.myCenter.3")}</div>
              </div>
            </div>
            <div className="desc">
              {it.profile} 
            </div>
            <div className="follow-sign">
               {translate('creator.myCenter.4')}
            </div>
           
          </div>
        </div>
      ))}
    </div>
  );
};

export const Followers: FC<{}> = () => {
  const { data = [], loading, load } = useRequest(getFollowers, { autoLoad: false });
  useEffect(() => { load({}) }, []);
  return (
    <MyCenterHeader>
      {loading ? <Loading size="mini" /> : !data.length ? <NoData /> : <FollowerList list={data} />}
    </MyCenterHeader>
  );
};

export const Following: FC<{}> = () => {
  const { id } = useParams<any>();
  const { data = [], loading, load } = useRequest(getFollowing, { autoLoad: false });
  useEffect(() => { load({ id }) }, [id]);
  return (
    <MyCenterHeader>
      {loading ? <Loading size="mini" /> : !data.length ? <NoData /> : <FollowerList list={data} />}
    </MyCenterHeader>
  );
}